/* Overlay */
@media screen and (min-width: 320px) {
  .overlay {
    position: fixed;
    font-size: 0.8rem;
    z-index: 9;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #1c160263;
    transform: translateX(-100vw);
    opacity: 0;
  }
  .overlay-slide-right {
    transition: all 0.4s ease-out;
    transform: translateX(0);
    opacity: 1;
  }
  .overlay-slide-left {
    transition: all 0.8s ease-in;
    transform: translateX(-100vw);
    opacity: 0;
  }
  nav,
  nav ul {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
  }

  nav ul {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    list-style: none;
  }

  nav ul li {
    height: 25%;
    width: 100vw;
    overflow: hidden;
    color: rgb(81, 81, 81);

    /* text-shadow: 0 0.3px 0.5px rgba(10, 10, 10, 0.491); */
  }

  nav li a {
    position: relative;
    top: 45%;
    background-color: white;
    padding: 12% 0 14% 0;
    color: black;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-decoration: none;
    display: block;
    text-align: center;
    transition: all 0.2s;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  nav li a:hover {
    /* color: var(--navColor3); */
    text-shadow: none;
    font-size: 1.2rem;
  }

  nav ul > li:hover {
    /* transform: scale(1.2) translateX(20px); */
    /* width: 42vw; */
    /* transition: all 0.2s ease-out; */
  }
  nav li:nth-of-type(1) {
    background-color: transparent;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.512); */
    /* box-shadow: 5px 15px 10px rgba(25, 25, 25, 0.272); */
  }

  nav li:nth-of-type(1):hover {
    background-color: #ffd22e;
    border-right: var(--navColor2);
  }

  nav li:nth-of-type(2) {
    background-color: transparent;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.512); */
    /* box-shadow: 5px 15px 10px rgba(25, 25, 25, 0.272); */
  }

  nav li:nth-of-type(2):hover {
    background-color: #ffd22e;
    border-right: var(--navColor2);
  }
  nav li:nth-of-type(3) {
    background-color: transparent;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.512); */
    /* box-shadow: 5px 15px 10px rgba(25, 25, 25, 0.272); */
  }
  nav li:nth-of-type(3):hover {
    background-color: #ffd22e;
    border-right: var(--navColor2);
  }
  nav li:nth-of-type(4) {
    background-color: transparent;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.512); */
    /* box-shadow: 5px 15px 10px rgba(25, 25, 25, 0.272); */
  }
  nav li:nth-of-type(4):hover {
    background-color: #ffd22e;
    border-right: var(--navColor2);
  }

  /* Slide in animation with delay for each */
  .slide-in-1 {
    -webkit-animation: slide-in 0.6s linear 0.1s both;
            animation: slide-in 0.6s linear 0.1s both;
  }

  .slide-in-2 {
    -webkit-animation: slide-in 0.6s linear 0.2s both;
            animation: slide-in 0.6s linear 0.2s both;
  }

  .slide-in-3 {
    -webkit-animation: slide-in 0.6s linear 0.3s both;
            animation: slide-in 0.6s linear 0.3s both;
  }

  .slide-in-4 {
    -webkit-animation: slide-in 0.6s linear 0.4s both;
            animation: slide-in 0.6s linear 0.4s both;
  }

  @-webkit-keyframes slide-in {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes slide-in {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  /* Slide out animation with delay for each */
  .slide-out-1 {
    -webkit-animation: slide-out 0.3s linear 0.5s both;
            animation: slide-out 0.3s linear 0.5s both;
  }

  .slide-out-2 {
    -webkit-animation: slide-out 0.3s linear 0.4s both;
            animation: slide-out 0.3s linear 0.4s both;
  }

  .slide-out-3 {
    -webkit-animation: slide-out 0.3s linear 0.3s both;
            animation: slide-out 0.3s linear 0.3s both;
  }

  .slide-out-4 {
    -webkit-animation: slide-out 0.3s linear 0.2s both;
            animation: slide-out 0.3s linear 0.2s both;
  }

  @-webkit-keyframes slide-out {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }

  @keyframes slide-out {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }
}

